import React from 'react'
import { commands } from '../cmdList'

const Command = ({command}) => {
  return (
    <div className='font-montserrat my-3 text-white grid grid-cols-5
    border-t pt-2 px-2 border-graple-100'>
      <h3 className='text-md col-span-1'>{command.name}</h3>
      <div className='col-start-3 col-end-6 text-right text-sm mb-1'>{command.description}</div>
      <div className='col-start-1 col-end-6 text-sm text-gray-400
      px-2 bg-graple rounded-md mt-1'>
        {command.name} {command.usage}
      </div>
    </div>
  )
}

export default Command