import React from 'react'
import { commands } from '../cmdList'
import Command from '../components/command'
import NavBarComponent from '../components/navbar'

const Commands = () => {
  const categories = Object.keys(commands).map((category) => {
    return category
  })
  return (
    //flex flex-col items-center
    <>
    <NavBarComponent/>
    <div className='font-montserrat text-white flex flex-col items-center mt-20'> 
      <div className='rounded-md mb-2'>
        <h1 className='text-3xl text-center'>Commands</h1>
        <p>Arguments inside "[ ]" are optional</p>
      </div>
      {Object.keys(commands).map((category) => {
        return (
          <div className='rounded-md w-11/12 mt-3 bg-graple-300 to-lopink md:max-w-4xl'>
            <h2 id={category} className='text-2xl text-center -mb-3 mt-1'>{category}</h2>
            {commands[category].map((command) => {
              return <Command command={command} />
            })}
          </div>
        )
      })}
    </div>
    </>
  )
}

export default Commands